footer {
    .logo {
        display: flex;
        margin-top: vw(171);
        margin-bottom: vw(186);
        align-items: center;
        justify-content: center;

        @include mobile {
            margin: vw(105) 0;
        }

        svg {
            width: vw(548);
            height: auto;

            @include mobile {
                width: vw(300);
            }
        }
    }

    .footer-content-bottom {
        display: flex;
        height: vw(44);
        color: #443826;
        font-weight: 500;
        font-size: vw(15);
        align-items: center;
        background-color: #EDEDED;
        justify-content: space-between;
        padding: 0 vw(var(--g-outer-padding));
        font-family: var(--font-secondary);

        @include mobile {
            height: auto;
            display: block;
            padding: vw(10) 0;
            text-align: center;
            justify-content: center;

        }


        &-b {
            color: #443826;

            @include mobile {
                padding-top: vw(6);
            }


            a {
                color: #443826;
            }
        }
    }
}